"use client";

import { useState, useEffect, useCallback } from 'react';
import { Wallet, TrendingUp, TrendingDown, FolderKanban, Maximize2, RefreshCw, X } from 'lucide-react';
import { DashboardLayout } from '@/components/layout/DashboardLayout';
import { MetricCard } from '@/components/dashboard/MetricCard';
import { RevenueChart } from '@/components/dashboard/RevenueChart';
import { ProjectDistributionChart } from '@/components/dashboard/ProjectDistributionChart';
import { BudgetComparisonChart } from '@/components/dashboard/BudgetComparisonChart';
import { UpcomingDeadlines } from '@/components/dashboard/UpcomingDeadlines';
import { LastReceived } from '@/components/dashboard/LastReceived';
import { ProjectTable } from '@/components/projects/ProjectTable';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';

export default function DashboardPage() {
  const [metrics, setMetrics] = useState<any>(null);
  const [revenue, setRevenue] = useState([]);
  const [distribution, setDistribution] = useState([]);
  const [budgetComparison, setBudgetComparison] = useState([]);
  const [lastReceived, setLastReceived] = useState([]);
  const [deadlines, setDeadlines] = useState([]);
  const [projects, setProjects] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear().toString());
  const [expandedCard, setExpandedCard] = useState<string | null>(null);

  const router = useRouter();
  const [isFetchingMetrics, setIsFetchingMetrics] = useState(false);

  const handleRefetchMetrics = async () => {
    setIsFetchingMetrics(true);
    await fetchMetrics();
    setIsFetchingMetrics(false);
  };
  // Individual Fetching Functions
  const fetchMetrics = useCallback(async () => {
    try {
      const res = await fetch('/api/dashboard/metrics');
      const data = await res.json();
      setMetrics(data);
    } catch (e) { console.error(e); }
  }, []);

  const fetchRevenue = useCallback(async (year: string) => {
    try {
      const res = await fetch(`/api/dashboard/revenue?year=${year}`);
      const data = await res.json();
      setRevenue(data);
    } catch (e) { console.error(e); }
  }, []);

  const fetchDistribution = useCallback(async () => {
    try {
      const res = await fetch('/api/dashboard/distribution');
      const data = await res.json();
      setDistribution(data);
    } catch (e) { console.error(e); }
  }, []);

  const fetchBudgetComparison = useCallback(async () => {
    try {
      const res = await fetch('/api/dashboard/budget-comparison');
      const data = await res.json();
      setBudgetComparison(data);
    } catch (e) { console.error(e); }
  }, []);

  const fetchLastReceived = useCallback(async () => {
    try {
      const res = await fetch('/api/dashboard/last-received');
      const data = await res.json();
      setLastReceived(data);
    } catch (e) { console.error(e); }
  }, []);

  const fetchDeadlines = useCallback(async () => {
    try {
      const res = await fetch('/api/dashboard/deadlines');
      const data = await res.json();
      setDeadlines(data);
    } catch (e) { console.error(e); }
  }, []);

  const fetchProjectsList = useCallback(async () => {
    try {
      const res = await fetch('/api/dashboard/projects');
      const data = await res.json();
      setProjects(data);
    } catch (e) { console.error(e); }
  }, []);

  // Initial load
  useEffect(() => {
    const initFetch = async () => {
      setLoading(true);
      await Promise.all([
        fetchMetrics(),
        fetchRevenue(selectedYear),
        fetchDistribution(),
        fetchBudgetComparison(),
        fetchLastReceived(),
        fetchDeadlines(),
        fetchProjectsList()
      ]);
      setLoading(false);
    };
    initFetch();
  }, [selectedYear, fetchMetrics, fetchRevenue, fetchDistribution, fetchBudgetComparison, fetchLastReceived, fetchDeadlines, fetchProjectsList]);

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('en-BD', {
      style: 'currency',
      currency: 'BDT',
      maximumFractionDigits: 0,
    }).format(amount);
  };

  if (loading) return <div className="p-10 text-center font-medium">Loading Dashboard Data...</div>;

  return (
    <DashboardLayout title="Dashboard" currentPath="/" onNavigate={(path) => router.push(path)}>

      {/* Expanded Card Overlay */}
      {expandedCard && (
        <div className="fixed inset-0 z-[100] bg-background/80 backdrop-blur-sm flex items-center justify-center p-6">
          <div className="bg-card border w-full max-w-6xl max-h-[90vh] overflow-auto rounded-xl shadow-2xl relative p-8">
            <Button
              variant="outline"
              size="icon"
              className="absolute top-4 right-4"
              onClick={() => setExpandedCard(null)}
            >
              <X className="h-4 w-4" />
            </Button>
            <div className="h-full w-full">
              {expandedCard === 'revenue' && <RevenueChart data={revenue} isExpanded />}
              {expandedCard === 'budget' && <BudgetComparisonChart data={budgetComparison} isExpanded />}
              {expandedCard === 'distribution' && <ProjectDistributionChart data={distribution} isExpanded />}
              {expandedCard === 'lastReceived' && <LastReceived data={lastReceived} isExpanded />}
              {expandedCard === 'deadlines' && <UpcomingDeadlines deadlines={deadlines} isExpanded />}
            </div>
          </div>
        </div>
      )}

      <div className="space-y-6">
        {/* Metric Cards - Refetch Only */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <div className="relative group">
            <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity">
              <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchMetrics}>
                <RefreshCw className="h-3.5 w-3.5" />
              </Button>
            </div>
            <MetricCard title="Total Budget" value={formatCurrency(metrics?.totalBudget || 0)} icon={Wallet} variant="primary" />
          </div>

          <div className="relative group">
            <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity">
              <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchMetrics}>
                <RefreshCw className="h-3.5 w-3.5" />
              </Button>
            </div>
            <MetricCard title="Total Received" value={formatCurrency(metrics?.totalReceived || 0)} icon={TrendingUp} variant="success" />
          </div>

          <div className="relative group">
            <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity">
              <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchMetrics}>
                <RefreshCw className="h-3.5 w-3.5" />
              </Button>
            </div>
            <MetricCard title="Total Remaining" value={formatCurrency(metrics?.totalRemaining || 0)} icon={TrendingDown} variant="warning" />
          </div>

          <div className="relative group">
            <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity">
              <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchMetrics}>
                <RefreshCw className="h-3.5 w-3.5" />
              </Button>
            </div>
            <MetricCard title="Active Projects" value={metrics?.activeCount?.toString() || "0"} icon={FolderKanban} />
          </div>
        </div>

        <Tabs defaultValue="charts" className="w-full">
          <TabsList className="glass-card mb-4">
            <TabsTrigger value="charts">Charts Overview</TabsTrigger>
            <TabsTrigger value="projects">All Projects</TabsTrigger>
          </TabsList>

          <TabsContent value="charts" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              {/* Revenue Chart */}
              <div className="relative group">
                <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity flex gap-1">
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={() => fetchRevenue(selectedYear)}>
                    <RefreshCw className="h-3.5 w-3.5" />
                  </Button>
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={() => setExpandedCard('revenue')}>
                    <Maximize2 className="h-3.5 w-3.5" />
                  </Button>
                </div>
                <div className="absolute top-4 right-16 z-10 flex items-center gap-2">
                  <span className="text-xs text-muted-foreground">Year:</span>
                  <Select value={selectedYear} onValueChange={setSelectedYear}>
                    <SelectTrigger className="h-8 w-[100px] bg-background/50 text-xs">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {[2023, 2024, 2025, 2026].map(year => (
                        <SelectItem key={year} value={year.toString()}>{year}</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
                <RevenueChart data={revenue} />
              </div>

              {/* Budget Comparison */}
              <div className="relative group">
                <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity flex gap-1">
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchBudgetComparison}>
                    <RefreshCw className="h-3.5 w-3.5" />
                  </Button>
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={() => setExpandedCard('budget')}>
                    <Maximize2 className="h-3.5 w-3.5" />
                  </Button>
                </div>
                <BudgetComparisonChart data={budgetComparison} />
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {/* Distribution */}
              <div className="relative group">
                <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity flex gap-1">
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchDistribution}>
                    <RefreshCw className="h-3.5 w-3.5" />
                  </Button>
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={() => setExpandedCard('distribution')}>
                    <Maximize2 className="h-3.5 w-3.5" />
                  </Button>
                </div>
                <ProjectDistributionChart data={distribution} />
              </div>

              {/* Last Received */}
              <div className="relative group">
                <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity flex gap-1">
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchLastReceived}>
                    <RefreshCw className="h-3.5 w-3.5" />
                  </Button>
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={() => setExpandedCard('lastReceived')}>
                    <Maximize2 className="h-3.5 w-3.5" />
                  </Button>
                </div>
                <LastReceived data={lastReceived} />
              </div>

              {/* Deadlines */}
              <div className="relative group">
                <div className="absolute top-2 right-2 z-20 opacity-0 group-hover:opacity-100 transition-opacity flex gap-1">
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchDeadlines}>
                    <RefreshCw className="h-3.5 w-3.5" />
                  </Button>
                  <Button variant="ghost" size="icon" className="h-7 w-7" onClick={() => setExpandedCard('deadlines')}>
                    <Maximize2 className="h-3.5 w-3.5" />
                  </Button>
                </div>
                <UpcomingDeadlines deadlines={deadlines} />
              </div>
            </div>
          </TabsContent>

          <TabsContent value="projects">
            <div className="relative group">
              <div className="absolute top-4 right-4 z-20 opacity-0 group-hover:opacity-100 transition-opacity">
                <Button variant="ghost" size="icon" className="h-7 w-7" onClick={fetchProjectsList}>
                  <RefreshCw className="h-3.5 w-3.5" />
                </Button>
              </div>
              <ProjectTable projects={projects.map((p: any) => ({
                ...p,
                name: p.projectName,
                clientName: p.client?.name || 'Unknown',
                signDate: p.startDate,
                totalBudget: Number(p.totalProjectValue)
              }))} />
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </DashboardLayout>
  );
}